import React, { useState } from 'react';
import InnerTextHolder from '../innerTextHolder/innerTextHolder';

const Presentation = props => {
    const text = "Born and raised in Åsa, Sweden. Have lived abroad since 2008. First Paris, and now Rome. Married with three kids.";
    const [textValue, setTextValue] = useState("");
    const [on, setOn] = useState(false);

    function timer(ms) {
        return new Promise(res => setTimeout(res, ms));
    }


    async function onShowClickedEventHandler() {
        const textArr = text.split("");
        let newText = "";
        for (let i = 0; i < textArr.length; i++) {
            newText += textArr[i];
            setTextValue(newText);
            await timer(5);
        }
        setTextValue("Born and raised in Åsa, Sweden. Have lived abroad since 2008. First Paris, and now Rome. Married with three kids.");
    }

    return (
        <InnerTextHolder>
            <header onClick={() => onShowClickedEventHandler()}>
                <h2>Who am I?</h2>
            </header>
            <p>{textValue}</p>
        </InnerTextHolder>
    )
}

export default Presentation;